#contacts {
    background-image: $bg-image-2;
    background-size: cover;
    margin-top: 80px;
    position: relative;
    box-shadow: 0 -10px 50px #555;
    text-align: center;
}
  
.pfp-container {
    @include flexCenter(nowrap);
    max-width: 350px;
    max-height: 350px;
    border-radius: 50%;
    margin: 50px 0 50px 0;
    box-shadow: 0 0 50px gray;
    overflow: hidden;
}
  
.pfp {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
}
  
.contact-form {
    background-color: $bg-color-1;
    position: relative;
    width: 400px;
    height: 350px;
    margin: 50px;
    padding: 10px 0 10px 0;
    border-radius: 35px;
    box-shadow: 0 -10px 50px gray;
}
  
.contact-section {
    @include flexCenter;
    width: 300px;
    margin: auto;
    margin-top: 30px;
}
  
.contact-header {
    font-family: 'adelia';
    font-weight: 100;
    font-size: 30px;
    line-height: 70px;
}
  
.contact-details {
    font-family: 'zai pencil';
    font-weight: bold;
    width: 80%;
    margin: 0.5em 0 0.25em 0;
    text-align: left;
    font-size: 1em;
}

.email-form {
    background-color: $bg-color-1;
    position: relative;
    width: 50%;
    min-width: 300px;
    max-width: 750px;
    height: 350px;
    margin: 50px;
    padding: 10px 0 10px 0;
    border-radius: 35px;
    box-shadow: 0 0 50px gray;
    text-align: left;
}

.email-label {
    width: 100%;
    text-align: center;
}

#email {
    border-radius: 20px;
    margin: 0 10% 10px 10%;
    width: 80%;
    padding: 5px 5px 5px 10px;
    font-family: 'zai pencil';
    box-shadow: 0 0 10px gray;
}

#message {
    margin-left: 10%;
    border-radius: 20px;
    padding: 10px;
    width: 65%;
    height: 70%;
    font-family: 'zai pencil';
    font-size: 1em;
    box-shadow: 0 0 10px gray;
}

#submit-email {
    color: white;
    position: relative;
    top: -107px;
    margin-left: 10px;
    width: 13%;
    border-radius: 20px;
    background-color: #be0705;
    font-weight: bold;
    font-size: 1.5rem;
    height: 70%;
    box-shadow: 0 0 10px gray;
}

#email-sent {
    margin-top: 70px;
    text-align: center;
}